import React, { useEffect, useState } from 'react';
import { Form, Input, Button, Typography, Space, Alert, Flex, Select, Divider } from 'antd';
import { useAuth } from '../../assistant/Contexts/AuthContext';
import { useNavigate } from 'react-router-dom';
import Logo from '../../shared/components/Logo';
import { insertMarkdownAsHtml } from '../assistant';
import Card from '../../shared/components/Card';
import { useApiClient } from '../../shared/hooks/useApiClient';
import { AddCircle12Filled } from '@fluentui/react-icons';

const { Title, Text } = Typography;

// Main App component
export default function AIAssistantForm() {
  const [form] = Form.useForm();
  const [myTopics, setMyTopics] = useState([]);
  // const inputNewTopicRef = useRef(null);
  const [newTopic, setNewTopic] = useState('');
  const [selectedTopicId, setSelectedTopicId] = useState('');
  const [prompt, setPrompt] = useState('');
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [isSuccess, setIsSuccess] = useState(true);
  const { logout, username } = useAuth();
  const navigate = useNavigate();
  const apiClient = useApiClient();

  useEffect(() => {
    async function fetchMyTopics() {
      const topics = await apiClient.getMyTopics();
      if (topics.isSuccess) {
        setMyTopics(topics.data);
      }
    }

    fetchMyTopics();
  }, []);

  async function onFinish() {
    try {
      setIsSubmitting(true);
      const response = await apiClient.postHuggingFace(prompt, selectedTopicId, myTopics.find(topic => topic.topicId === selectedTopicId)?.topic);

      if (response.isSuccess && response.data.choices.length) {
        const { content, topicId } = response.data.choices[0].message;
        await insertMarkdownAsHtml(content);
        form.resetFields(['prompt']);
        // This is the new topic we just created, and it has no topicId yet, hence we need to update it
        if (selectedTopicId === '') {
          setMyTopics(myTopics => {
            for (const myTopic of myTopics) {
              if (myTopic.topicId === '') {
                myTopic.topicId = topicId;
                break;
              }
            }
            return myTopics;
          });
          setSelectedTopicId(topicId);
        }
      }

      setIsSuccess(response.isSuccess && response.data.choices.length);
    } catch (error) {
      setIsSuccess(false);
      logout();
      navigate("/login");
    } finally {
      setIsSubmitting(false);
    }
  }

  return (
    <Card>
      <Space direction="vertical" style={{ width: '100%', marginBottom: '24px' }}>
        <Logo />
        <Flex gap="small">
          <Title level={5}>Welcome {username}!</Title>
          <Button
            type='link'
            danger
            onClick={() => {
              logout();
              navigate("/login");
            }}
            size="small"
          >
            Log Out
          </Button>
        </Flex>
        <Text type="secondary">What are you looking for right now?</Text>
      </Space>

      <Form
        form={form}
        name="assistant"
        onFinish={onFinish}
        layout="vertical"
      >
        <Form.Item
          label="Topic"
          name="topic"
          initialValue={{ topic: selectedTopicId }}
          rules={[{ required: true, message: "Select a topic or create one" }]}>
          <Select
            placeholder="Select a topic or create one"
            // popupRender={(menu) => (
            //   <>
            //     {menu}
            //     <Divider style={{ margin: '8px 0' }} />
            //     <Input
            //       placeholder="New topic"
            //       // ref={inputNewTopicRef}
            //       value={newTopic}
            //       onChange={e => setNewTopic(e.target.value)}
            //       onKeyDown={(e) => e.stopPropagation()}
            //     />
            //     {
            //       myTopics.filter(topic => topic.topicId === '').length === 0 && (
            //         <Button type="text" icon={<AddCircle12Filled />} onClick={e => {
            //           e.preventDefault();
            //           if (newTopic) {
            //             setMyTopics(topics => [...topics, { topicId: '', topic: newTopic }]);
            //           }
            //         }}>
            //           New Topic
            //         </Button>
            //       )
            //     }
            //   </>
            // )}
            onChange={val => setSelectedTopicId(val)}
            // defaultValue={selectedTopicId}
            options={myTopics.map((topic) => ({ label: topic.topic, value: topic.topicId }))}
          />
        </Form.Item>
        <Form.Item
          label="Prompt"
          name="prompt"
          rules={[
            { required: true, message: 'Please input your prompt!' }
          ]}
        >
          <Input.TextArea rows={7} placeholder="Your prompt here" onChange={e => setPrompt(e.target.value)} value={prompt} />
        </Form.Item>
        <Form.Item>
          <Button type="primary" htmlType="submit" loading={isSubmitting} disabled={isSubmitting} block size="large">
            Generate
          </Button>
          {
            !isSuccess && !isSubmitting && <Alert
              message="Operation Failed!"
              description="Oop ! Operation failed! Please try again later."
              type="error"
              showIcon
              style={{ marginTop: 8 }}
            />
          }
        </Form.Item>
      </Form>
    </Card>
  );
}